.aboutUsDiv {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom:2px solid #c1272d;
    border-right: 2px solid #c1272d; 
    border-top: 2px solid #c1272d;
    position: absolute;
    top: 24vh;
    left: 15vw;
    width: 27vw;
    background: white;
    z-index: 1;
    transition: 0.5s cubic-bezier(0.4, 0, 0.4, 1);
  }
  
  .aboutUsStatementDiv {
    font-size: 1.2rem;
    font-weight: 300;
    line-height: 1.6em;
    margin: 2rem;
  }
  
  .missionAndBoard{
    background-color: #c1272d;
    width: 60%;
    height: 3%;
    margin: .5rem;
    border: none;
  }
  
  
  .missionAndBoardText {
    color: white;
    font-size: 1rem;
    font-weight: 500;
    padding: 0 3rem;
    text-transform: uppercase;
    white-space: nowrap;
  }